import {
		mapState,
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			id:'',
			name:'',
			text:'',
			pic:'',
			share:0
		}
	},
	methods: {
		getTask() {
			let data = {
				id: this.id,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/task/todo',data,'GET').then(res => {
				if(res.data.code === 1000){
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
					this.share = 1
					uni.setClipboardData({
					    data: this.text,
					});
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
				}
			})
		},
		copyText() {
			uni.setClipboardData({
			    data: this.text,
			    success: function () {
					uni.showToast({
						title: '复制成功',
						duration:2000,
						icon:"none"
					})
			    }
			});
		},
		shareCancel() {
			this.share = 0
		},
		shareWeixin() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareFriends() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
	},
	onHide() {
		this.share = 0
	},
	onLoad(option) {
		this.id = option.id
		this.name = option.name
		let data = {
			id: this.id,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/task/preview',data,'GET').then(res => {
			this.text = res.data.retval.copy_text
			this.pic = res.data.retval.image_group[0]
		})
	},
	onReady() {
		uni.setNavigationBarTitle({
			title:this.name
		})
	}
}
